import {
  selectSceneSelected,
  selectThemeConfigGlobal,
} from '@containers/Home/selectors';
import { I_Scene } from '@containers/Home/types';
import { Tooltip } from 'antd';
import { memo, useState, useCallback } from 'react';
import { useSelector } from 'react-redux';
import { MultipleStagingThumbWrapper } from './styled';

interface Props {
  scenes?: I_Scene[];
  callKrpano?: (xml: string) => void;
}

const MultipleStagingThumb = memo(({ scenes, callKrpano }: Props) => {
  const [indexActive, setIndexActive] = useState(0);

  const sceneSelected = useSelector(selectSceneSelected);
  const themeConfig = useSelector(selectThemeConfigGlobal);

  const onChangeSceneInternal = useCallback(
    (index: number) => {
      setIndexActive(index);
      callKrpano &&
        callKrpano(
          `loadscene('scene_0${index}', null, MERGE|KEEPMOVE|KEEPVIEW|KEEPHOTSPOTS, BLEND(${0.5}, easeInCubic))`,
        );
    },
    [callKrpano],
  );

  return (
    <MultipleStagingThumbWrapper className={themeConfig?.selected || ''}>
      <div className="--list">
        <Tooltip placement="left" title={sceneSelected?.title || 'N/A'}>
          <div
            className={`--item ${indexActive === 0 ? 'active' : ''}`}
            onClick={() => onChangeSceneInternal(0)}
          >
            <img src={sceneSelected?.media?.src_thumb || ''} alt="" />
            <p className="">{sceneSelected?.title || 'N/A'}</p>
          </div>
        </Tooltip>

        {scenes?.map((scene, index) => (
          <Tooltip placement="left" title={scene.title || 'N/A'} key={scene.id}>
            <div
              className={`--item ${indexActive === index + 1 ? 'active' : ''}`}
              onClick={() => onChangeSceneInternal(index + 1)}
            >
              <img src={scene.media?.src_thumb || ''} alt="" />
              <p className="">{scene.title || 'N/A'}</p>
            </div>
          </Tooltip>
        ))}
      </div>
    </MultipleStagingThumbWrapper>
  );
});

export default MultipleStagingThumb;
